*{
    margin:0;
    padding:0;
    box-sizing: border-box;


}
.spinner{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.386);
}
.main{
    padding: 10px;
    height: 100vh;
    
}
.layout{
    display: flex;
}
.sidebar{
min-height: 100%;
width: 300px;
border-radius: 5px;
background-color:#145e7b;
box-shadow:0 0 2px gray;
margin-right: 20px;
color: black;
}
.content{
    width: 100%;
    height: 100%;
}
.header{
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px gray;
    background-color: white;
}
.body{
    height: 85vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px gray;
    background-color: white;
}
.logo h6{
    font-size: 1.5rem;
    text-align: center;
    margin:20px 0px;
}
.menu{
    margin-top: 50px;
}
.menu-item{
    margin-top: 30px;
}
.menu-item a{
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}
.menu-item i{
    font-size:1.2rem;
    margin: 0 15px;
}
.active{
    background-color: white;
padding: 10px;

}
.active a,.active i{
    color:#145e7b;
}
.header-content{
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0 15px;
}
.header-content i{
    margin-right: 20px;
    font-size: 1.2rem;

}
.header-content a{
    text-decoration: none;
    font-size: 1.2rem;
    color: red;
    margin: 0 10px;
    text-transform: uppercase;
}
.form-container{
    display: flex;
    align-items: center;
    justify-content: center;
    height:100vh; 
   
}
.registerform{
    border: 1 px solid black;
    width: 30%;
    padding:30px;
}
